import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";
import { VapiAssistant } from "@/types/vapi";

// Types
export interface AssistantConfig {
  name: string;
  firstMessage: string;
  transcriber: {
    provider: string;
    model: string;
  };
  model: {
    provider: string;
    model: string;
    temperature?: number;
  };
  voice: {
    provider: string;
    voiceId: string;
  };
  systemMessage?: string;
  functions?: any[];
  endCallMessage?: string;
  endCallPhrases?: string[];
  metadata?: Record<string, any>;
}

export interface Assistant {
  id: string;
  assistant_id?: string;
  name: string;
  firstMessage: string;
  transcriber: any;
  model: any;
  voice: any;
  systemMessage?: string;
  functions?: any[];
  endCallMessage?: string;
  endCallPhrases?: string[];
  metadata?: Record<string, any>;
  createdAt: string;
  updatedAt: string;
}

// Get all assistants with pagination
export async function fetchAllAgents(page: number, search: string) {
  try {
    const res = await axiosInstance.get("/api/assistant/get_all_assistants", {
      params: { page, search },
    });
    return res.data;
  } catch (error: any) {
    console.error("Error fetching agents:", error);

    // Don't show toast error for expected scenarios:
    // - 404: No agents found (normal for new users)
    // - 204: Empty results
    // - 402: Payment required (normal for users without credits)
    const expectedStatuses = [404, 204, 402];
    if (!expectedStatuses.includes(error?.response?.status)) {
      toast.error("Failed to fetch agents");
    }

    // Return empty result instead of throwing for expected scenarios
    if (expectedStatuses.includes(error?.response?.status)) {
      return { data: [], totalAssistant: 0, currentPage: 1, totalPages: 0 };
    }

    throw error;
  }
}

// Get all assistants without pagination (fetch all pages)
export async function fetchAllAgentsComplete(search: string = "") {
  try {
    let allAssistants: Assistant[] = [];
    let currentPage = 1;
    let totalPages = 1;

    do {
      const res = await axiosInstance.get("/api/assistant/get_all_assistants", {
        params: { page: currentPage, search, limit: 50 }, // Increase limit for efficiency
      });

      const { data, totalPages: total } = res.data;
      allAssistants = [...allAssistants, ...data];
      totalPages = total;
      currentPage++;
    } while (currentPage <= totalPages);

    return { data: allAssistants, total: allAssistants.length };
  } catch (error: any) {
    console.error("Error fetching all agents:", error);

    // Don't show toast error for expected scenarios:
    // - 404: No agents found (normal for new users)
    // - 204: Empty results
    // - 402: Payment required (normal for users without credits)
    const expectedStatuses = [404, 204, 402];
    if (!expectedStatuses.includes(error?.response?.status)) {
      toast.error("Failed to fetch all agents");
    }

    // Return empty result instead of throwing for expected scenarios
    if (expectedStatuses.includes(error?.response?.status)) {
      return { data: [], total: 0 };
    }

    throw error;
  }
}

