// Get assistant by ID
export async function getAssistantById(id: string) {
  try {
    const res = await axiosInstance.get(`/api/assistant/get-assistants/${id}`);
    return res.data;
  } catch (error: any) {
    console.error("Error fetching assistant:", error);

    // Don't show toast error for expected scenarios:
    // - 404: Assistant not found (normal for new users or invalid IDs)
    // - 204: Empty results
    // - 402: Payment required (normal for users without credits)
    const expectedStatuses = [404, 204, 402];
    if (!expectedStatuses.includes(error?.response?.status)) {
      toast.error("Failed to fetch assistant");
    }

    // Return null instead of throwing for expected scenarios
    if (expectedStatuses.includes(error?.response?.status)) {
      return null;
    }

    throw error;
  }
}

// Get complete assistant details from VAPI directly
export async function getVapiAssistantById(id: string) {
  try {
    const res = await axiosInstance.get(`/api/assistant/get-assistants/${id}`);
    return res.data;
  } catch (error) {
    console.error("Error fetching VAPI assistant:", error);
    throw error;
  }
}

// Create new assistant
export async function createAssistant(data: VapiAssistant) {
  try {
    const res = await axiosInstance.post("/api/assistant/create_assistant", data);
    toast.success("Assistant created successfully");
    return res.data;
  } catch (error: any) {
    console.error("Error creating assistant:", error);
    const message = error.response?.data?.message || "Failed to create assistant";
    toast.error(message);
    throw error;
  }
}

// Update assistant
export async function updateAssistant(id: string, data: Partial<VapiAssistant>) {
  try {
    const res = await axiosInstance.put(`/api/assistant/update-assistant/${id}`, data);
    return res.data;
  } catch (error: any) {
    console.error("Error updating assistant:", error);
    const message = error.response?.data?.message || "Failed to update assistant";
    toast.error(message);
    throw error;
  }
}

